import axios from 'axios'
import {Message} from 'element-ui'

axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
axios.defaults.baseURL = 'https://blog-server.hunger-valley.com'
axios.defaults.withCredentials = true
/*
//我们做的是前后端分离的项目，前端和后端在不同的服务器上
//登陆注册涉及到鉴权问题，会带有cookie，就存在跨域的
//问题。  举个例子，假设把代码部署带github上，服务端的接口
//又是上面的接口，此时别人访问github上的网站，服务端就涉及到跨域了。
//服务端可以设置为允许跨域或者哪一个域名允许跨域。 接口的跨域不会带上
//cookie。通过接口这次登陆后下次发请求也不会带上cookie，因为是跨域的请求。
以上就是  axios.default.withVredentials = true 的作用，可以解决不会带上
cookie这个问题。 
*/
export default function request(url, type = "get", data = {}){
  return new Promise((resolve, reject) =>{
    let  option ={
      url,
      method: type
    }
    if (type.toLowerCase() === 'get' ){
      option.params =data 
    }else{
      option.data = data
    }
    //参数写好后调用axios的then
    axios(option).then(res=>{
      if(res.data.status === 'ok'){
        resolve(res.data)
      }else{
        Message.error(res.data.msg)
        //这里不能用this.message  因为不是vue实例。
        reject(res.data)
      }
    }).catch( (err) =>{
      Message.error('网络异常')
      reject({ msg: '网络异常'})
    })
  })
}


