import Vue from 'vue'
import Vuex from 'vuex'
import auth from './modules/auth'    //和用户登录注册相关代码放到auth中
import blog from './modules/blog'    //和blog操作相关的东西放到blog中

Vue.use(Vuex)

export default new Vuex.Store({         //创建一个store对象，这个对象就可以供全局的对象vue使用
  modules:{
    auth,
    //这是解构赋值,相当于auth: auth
    blog
  }
})



