import auth from '@/api/auth'


let state ={
  //用户信息,比如注册或登陆后的信息：id 用户名 头像 登录时间
  user: null,
  isLogin: false
  
}
const getters ={        //减少层级，方便引用。
  user: state=> state.user,
  isLogin: state => state.isLogin
}
const mutations ={   //必须是同步的
  setUser(state, payload){
    state.user = payload.user
  },
  setLogin(state, payload){
    state.isLogin = payload.isLogin
  }
}
const actions = {   
  login( {commit}, { username, password}){
    return auth.login({ username,password })
      .then( (res)=>{
        commit('setUser', {user: res.data}),
        commit('setLogin', {isLogin: true})
      })

  },
  async register({commit},{username, password}){
    // console.log('this')
    // console.log(commit)
    let res = await auth.register({username, password})
    //res相当于promise执行后then的结果
    commit('setUser', {user: res.data}),
    commit('setLogin', {isLogin: true})
    //等价于上面的login方法
    return res.data
  },
  async logout({ commit}){
    await auth.logout()
    commit('setUser',{ user: null})  //退出的时候设置用户名为null
    commit('setLogin',{isLogin: false})
  },
  async checkLogin( {commit, state}){
    if(state.isLogin ) return true
      let res = await auth.getInfo()  //判断用户是否登录
    commit('setLogin', {isLogin: res.isLogin})  //看看现在是什么状态
    if(!res.isLogin) return false  //如果没有登录返回false
    commit('setUser', {user: res.data})  //否则执行setUser,传res.data回去
    return true
  }
}

/*
  this.logout().then(isLogin=>{
     //islogin 就是上面return 的true或false
  })
*/

//和用户登录注册相关代码放到auth中

export default {
  state,
  getters,
  mutations,
  actions
  //es6语法
}


