// 页面在此启动
<template>
  <div id="app">
    <Header id="header"></Header>
    <main id="main">
      <router-view/>
    </main>
    <Footer id='footer'></Footer>
  </div>
</template>

<script>
import Header from "@/components/header.vue";
import Footer from "@/components/footer.vue";

export default {
  name: "App",
  components: {
    //不要忘记哟
    Header,
    Footer
  }
};
</script>

<style lang='less'>



//不能单独写#pp高度，因为不继承
html,body,#app{
  height: 100%; 
}
#app {
  display: grid;
  grid-template-columns: 12% auto 12%;
  //三列，两边12剩下的中间撑开。
  grid-auto-rows: auto 1fr auto;
  //三行，第一行（header）由里面的内容撑开  第二行（footer）尽可能占满剩下的高度
  grid-template-areas:
    "header header header" // 望结构生义，三行三列，第一行三列都是header
    " .     main   .     "
    "footer footer footer";
  #header {
    grid-area: header;
    padding-left: 12%;
    padding-right: 12%;
  }
  #main {
    grid-area: main;
  }
  #footer {
    grid-area: footer;
    padding-left: 12%;
    padding-right: 12%;
  }
}

@media (max-width: 768px){
  #app{
    grid-template-columns: 10px auto 10px;  
    
    #header, #footer{
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}
.el-button--default {
  color: @themeColor;
  border: 1px solid @themeLighterColor;
  background: #fff;
  border-radius: 5px;  
}
@import "./assets/common.less";   //分号不能忘
</style>



