<template>
  <header :class="{ login: isLogin, 'no-login': !isLogin}">
                  <!-- key对应的值是true，就添加类名key -->
    <template v-if="!isLogin">
      <router-link to='/'> <h1>来分享你的知识吧！</h1></router-link>
      <p>精品博客汇聚</p>
      <div class="btns" >  <!-- //v-if="$route.path!=='/login' && $route.path!=='/register'" -->
        <router-link to='/login'><el-button>立即登陆</el-button></router-link>
        <router-link to='/register'><el-button>注册账号</el-button></router-link>
      </div>
    </template>
    <template v-if="isLogin">
      <h1><router-link to='/'>来分享你的知识吧！</router-link></h1>
      <router-link to='/Create'>
        <i class="edit el-icon-plus"></i>
      </router-link>
      <div class="user">
        <img class='avatar' :src="user.avatar" :alt="user.username" :title="user.username">
        <ul>
          <li><router-link to="/my">我的</router-link> </li>
          <li><a href="#" @click='onLogout'>注销</a> </li>
        </ul>
      </div>
    </template>
  </header>

</template>

<script>

import auth from '@/api/auth'
// window.auth = auth

import { mapGetters, mapActions} from 'vuex'

export default {
  data(){
    return {
      // : false
    }
  },
  computed:{
    ...mapGetters([
      'isLogin',
      'user'
    ])
  },
  created(){   //组件创建的时候，模板还没挂载（渲染）时
    this.checkLogin()   //promise对象
  },
  methods: {
    ...mapActions([
      'checkLogin',    //相当于把当前的checkLogin变成store>modules>auth中的方法
      'logout'
    ]),
  onLogout(){
    this.logout()
  }
  },
}
</script>


<style  lang="less">
  @import '../assets/base.less';
  header.no-login{
    padding: 0 12% 30px 12%;
    background: @bgColor;
    display: grid;
    justify-items: center;

    h1{
      font-size: 40px;
      margin: 60px 0 0 0;
      text-transform: uppercase;
      color: rgb(51, 51, 51);
      a{
        color: #fff;
      }
    }

    p{
      margin: 15px 0 0 0 ;
      color: #fff;   
    }

    button{
      margin: 20px 5px 0;
    }
  }
  header.login{
    display: flex;
    align-items: center;
    background: @bgColor;
    h1{
      margin: 0;
      padding: 0;
      color: #fff;
      font-size:  40px;
      text-transform: uppercase;
      flex:1;

      a{
        text-decoration: none;
        color: #fff;
      }
    }
    .edit{
      color: #fff;
      font-size: 30px;
    }
    .avatar{
      width: 40px;
      height: 40px;
      border: 1px solid transparent;
      border-radius: 50%;
      margin-left: 15px;
    }
  }
   .user {
    position: relative;

    ul {
      display: none;
      position: absolute;
      right: 0;
      list-style: none;
      border: 1px solid #eaeaea;
      margin:0;
      padding: 0;
      background-color: #fff;

      a {
        text-decoration: none;
        color: #333;
        font-size: 12px;
        display: block;
        padding: 5px 10px;

        &:hover {
          background-color: #eaeaea;
        }
      }

    }

    &:hover ul {
      display: block;
    }
  }
</style>



